<template>
	<!-- 个人信息页面 -->
   <div class="userInfo">
	   <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
	     <el-tab-pane label="个人信息" name="userInfo">
	       <div>
	         <userCom :data="data"></userCom>
	       </div>
	     </el-tab-pane>
	    <el-tab-pane label="修改密码" name="updatepwd">
	        <div>
	         <pwdChange></pwdChange>
	       </div>
	     </el-tab-pane>
	   </el-tabs>
   </div>
</template>

<script>
import userCom from '../../../../components/common/userCom.vue'
import pwdChange from '../../../../components/common/pwdChange.vue'
import data from '../../../../assets/config/data.json'
export default {
	name:'userInfo',
	data(){
		return {
			activeName: 'userInfo',
			data:''
		}
	},
	components:{
		userCom,
		pwdChange
	},
	mounted() {
		// console.log('1')
		this.data=data.user
	},
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
    }
}
</script>

<style scoped>

</style>



