<template>
   <div class="pwdChange">
	   <el-row class="pwdbox">
	     <el-col>
			 <el-col :span="4" class="comText">
			   旧密码：
			 </el-col>
			 <el-col :span="5" class="comText">
				 <el-input
				   v-model="input1">
				 </el-input>
			 </el-col>
		 </el-col>
		 <el-col>
		 			 <el-col :span="4" class="comText">
		 			   新密码：
		 			 </el-col>
		 			 <el-col :span="5" class="comText">
		 				 <el-input
		 				   v-model="input2">
		 				 </el-input>
		 			 </el-col>
		 </el-col>
		 <el-col>
		 			 <el-col :span="4" class="comText">
		 			   密码确认：
		 			 </el-col>
		 			 <el-col :span="5" class="comText">
		 				 <el-input
		 				   v-model="input3">
		 				 </el-input>
		 			 </el-col>
		 </el-col>
		 <el-col class="btns">
			 <el-col :span="9">
				 <el-button type="primary" @click="qr">确认</el-button>
			     <!-- <el-button>取消</el-button> -->
			 </el-col>
		 </el-col>
	   </el-row>
		
   </div>
</template>

<script>
export default {
	name:'pwdChange',
	data(){
		return {
			input1:'',
			input2:'',
			input3:''
		}
	},
	methods:{
		qr(){
			 this.$message({
			          message: '恭喜你，修改成功！',
			          type: 'success'
			        });
		}
	}
}
</script>

<style scoped>
	@import '../../assets/css/pwdChange.css';
</style>



